import React, { useState, useEffect } from 'react'
import {
    Form, Input, Button, Select, InputNumber, Switch, Divider, Space, DatePicker,
    // TimePicker, 
    Tooltip, Row, Col
} from 'antd';
import Api from '../../helpers/api';
import { message } from "antd";
import {
    ReloadOutlined,
    ArrowLeftOutlined,
    SaveOutlined,
    InfoCircleOutlined
} from '@ant-design/icons';
import handleApiError from '../../helpers/handleApiError';
import moment from 'moment';

const api = new Api();
const { Option } = Select;

const FormMaterial = (props) => {

    const [errors, setErrors] = useState({})
    const [loading, setLoading] = useState(false)
    const [form] = Form.useForm();
    const [classification, setClassification] = useState([])
    const [classificationParameters, setClassificationParameters] = useState([])
    const [valueTimeAndDatePicker, setValueTimeAndDatePicker] = useState({})
    const [uom, setUom] = useState([])
    const [uomName, setUomName] = useState('')
    const [quantityUom, setQuantityUom] = useState(1)
    const [checkedSerialNumber, setCheckedSerialNumber] = useState(false)

    useEffect(() => {
        const { view, edit } = props
        if (view || edit) {
            getEditData()
        }

        fetchClassification()
        fetchUom()
    }, []);

    const onFinish = (data) => {

        setLoading(true)

        let params = []
        if (data.parameters.length > 0) {
            data.parameters.map((item, index) => {
                if (item) {
                    params.push({
                        [index]: valueTimeAndDatePicker[index] ? valueTimeAndDatePicker[index] : item
                    })
                }
            })
        }
        data.quantity_uom = quantityUom
        data.parameters = params
        data.serial_number = checkedSerialNumber

        let apiPost
        //handling if form add
        if (props.add) {
            apiPost = api.storeMaterial(data)
        } else if (props.edit) {
            //handling if form edit
            const { id } = props.match.params
            apiPost = api.updateMaterial(id, data)
        }

        apiPost.then(response => {
            setLoading(false)
            const data = response.data;
            message.success('save data success');

            if (props.add) {
                props.history.push({
                    pathname: `/material/edit/${data.id_hash}`,
                    state: { activeTab: 2 }
                })
                return
            }
            props.history.goBack();

        }).catch(error => {
            setLoading(false)
            if (error.response && error.response.data) {
                const { errors } = error.response.data
                setErrors(errors)
                // message.error(message);
            }
            handleApiError(error, props)
        });
    };

    const getEditData = () => {
        const { id } = props.match.params

        api.viewMaterial(id)
            .then(response => {
                const {
                    material_code, description, classification_id, classification_id_hash, serial_number,
                    unit_of_measurement_id, classification, material_parameters, quantity_uom, uom
                } = response.data

                setUomName(uom ? uom.name : '')
                setClassificationParameters(classification.parameters)
                setQuantityUom(quantity_uom)

                let parameters = []
                if (material_parameters.length > 0) {
                    let dataTimeAndDatePicker = { ...valueTimeAndDatePicker }
                    material_parameters.map(item => {
                        if (item.classification_parameter_id) {
                            if (item.classification_parameter.type == 2) {
                                // using moment for valid date in defaultvalue
                                parameters[item.classification_parameter_id] = moment(item.value, 'YYYY-MM-DD');

                                // set value time picker or date picker
                                dataTimeAndDatePicker[item.classification_parameter_id] = item.value
                                setValueTimeAndDatePicker(dataTimeAndDatePicker)
                            } else if (item.classification_parameter.type == 3) {
                                // using moment for valid date in defaultvalue
                                parameters[item.classification_parameter_id] = moment(item.value, 'HH:mm:ss');

                                // set value time picker or date picker
                                dataTimeAndDatePicker[item.classification_parameter_id] = item.value
                                setValueTimeAndDatePicker(dataTimeAndDatePicker)
                            } else {
                                parameters[item.classification_parameter_id] = item.value;
                            }
                        }
                    })
                }
                setCheckedSerialNumber(serial_number)
                form.setFieldsValue({
                    material_code,
                    description,
                    classification_id,
                    unit_of_measurement_id,
                    quantity_uom,
                    parameters
                });
            }).catch(error => {
                handleApiError(error, props)
            })
    }

    const handleReset = () => {
        form.setFieldsValue({
            material_code: '',
            description: '',
            classification_id: '',
            unit_of_measurement_id: '',
            parameters: [],
        });
        setCheckedSerialNumber(false)
        setClassificationParameters([])
    }

    const handleSearchClassification = (value) => {
        fetchClassification(value)
    }

    const handleChangeClassification = (classificationId) => {
        showClassification(classificationId)
        fetchClassification('')
    }

    const fetchClassification = (q) => {
        api.getClassificationMaterial({ q: q }).then(response => {
            const { data } = response.data;
            setClassification(data);
        }).catch(err => {
            console.log(err)
        })
    };

    const showClassification = (id) => {
        api.viewClassificationMaterial(id)
            .then(response => {
                const { id, parameters } = response.data
                setClassificationParameters(parameters)
            }).catch(error => {
                handleApiError(error, props)
            })
    }

    const handleChangeTimeAndDatePicker = (value, valueString, id) => {
        let dataTimeAndDatePicker = { ...valueTimeAndDatePicker }
        dataTimeAndDatePicker[id] = valueString
        setValueTimeAndDatePicker(dataTimeAndDatePicker)
    }

    const fetchUom = (q) => {
        api.getUom({ q: q }).then(response => {
            const { data } = response.data;
            setUom(data);
        }).catch(err => {
            console.log(err)
        })
    };

    const handleSearchUom = (value) => {
        fetchUom(value)
    }

    const handleChangeUom = (value) => {
        let uomName = uom.filter(val => val.id == value);
        setUomName(uomName[0].name)
        fetchUom('')
    }

    const onChangeSerialNumber = (value) => {
        setCheckedSerialNumber(value)
    }

    return (
        <Form name="complex-form" onFinish={onFinish} labelCol={{ span: 4 }} wrapperCol={{ span: 8 }}
            form={form}>

            <Form.Item name="material_code" label="Name"
                validateStatus={errors && errors.material_code ? 'error' : undefined}
                help={errors ? errors.material_code : null}
                rules={[{ required: true, message: 'Please input material code' }]}>
                <Input />
            </Form.Item>

            <Form.Item name="description" label="Description"
                validateStatus={errors && errors.description ? 'error' : undefined}
                help={errors ? errors.description : null}
                rules={[{ required: true, message: 'Please input description' }]}>
                <Input />
            </Form.Item>

            <Form.Item name="classification_id" label="Classification"
                validateStatus={errors && errors.classification_id ? 'error' : undefined}
                help={errors ? errors.classification_id : null}
                rules={[{ required: true, message: 'Please select classification' }]}>
                <Select
                    placeholder="Select"
                    filterOption={false}
                    showSearch
                    onSearch={handleSearchClassification}
                    onChange={handleChangeClassification}
                    name="classification_id">

                    {classification.map(item => (
                        <Option key={item.id} value={item.id}>{item.name}</Option>
                    ))}
                </Select>
            </Form.Item>

            <Form.Item name="unit_of_measurement_id" label="UoM"
                validateStatus={errors && errors.unit_of_measurement_id ? 'error' : undefined}
                help={errors ? errors.unit_of_measurement_id : null}
                rules={[{ required: true, message: 'Please select UoM' }]}>
                <Select
                    placeholder="Select"
                    filterOption={false}
                    showSearch
                    allowClear
                    onSearch={handleSearchUom}
                    onChange={(value) => handleChangeUom(value)}
                    name="unit_of_measurement_id">

                    {uom.map(item => (
                        <Option key={item.id} value={item.id}>{item.name}</Option>
                    ))}
                </Select>
            </Form.Item>

            {uomName &&
                <>
                    <Form.Item name="quantity_uom" label={`Quantity per ${uomName}`}
                        validateStatus={errors && errors.quantity_uom ? 'error' : undefined}
                        help={errors ? errors.quantity_uom : null}
                        rules={[{ required: false, message: 'Please input quantity UoM' }]}>
                        <Space>
                            <InputNumber min={1} value={quantityUom} onChange={setQuantityUom} />
                            <Tooltip placement="topLeft" title="Isi quantity per satuan, contoh per box isi 100 pcs, maka diisi 100">
                                <Button shape="circle" icon={<InfoCircleOutlined />} />
                            </Tooltip>
                        </Space>
                    </Form.Item>
                </>
            }

            {/* <Form.Item name="serial_number"
                label="Require Serial Number?">
                <Space>
                    <Switch onChange={onChangeSerialNumber} checked={checkedSerialNumber} />
                    <Tooltip placement="topLeft" title="Jika diaktifkan, maka pada saat Stock Opname atau Delivery Oder dengan material ini. Wajib mengisi serial number">
                        <Button shape="circle" icon={<InfoCircleOutlined />} />
                    </Tooltip>
                </Space>
            </Form.Item> */}

            {
                classificationParameters.length > 0 ?
                    classificationParameters.map((item, index) => {

                        if (item.type == 2) {
                            return (
                                < Form.Item key={index} name={['parameters', item.id]} label={item.name}
                                    rules={[{ required: false, message: `Please input ${item.name}` }]}>
                                    <DatePicker
                                        onChange={(item.id, (date, dateString) => {
                                            handleChangeTimeAndDatePicker(date, dateString, item.id)
                                        })}
                                        format="YYYY-MM-DD"
                                        style={{ width: '100%' }} />
                                </Form.Item>
                            )
                        } else if (item.type == 3) {
                            return (
                                < Form.Item key={index} name={['parameters', item.id]} label={item.name}
                                    rules={[{ required: false, message: `Please input ${item.name}` }]}>
                                    {/* <TimePicker
                                        onChange={(item.id, (date, dateString) => {
                                            handleChangeTimeAndDatePicker(date, dateString, item.id)
                                        })}
                                        format="HH:mm:ss"
                                        style={{ width: '100%' }} /> */}
                                </Form.Item>
                            )
                        } else if (item.type == 4) {
                            return (
                                <Form.Item key={index} name={['parameters', item.id]} label={item.name}
                                    rules={[{ required: false, message: `Please input ${item.name}` }]}>
                                    <InputNumber
                                        className="mnd-input-number"
                                        min={0}
                                        formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                        parser={value => value.replace(/\$\s?|(,*)/g, '')} />
                                </Form.Item>
                            )
                        } else if (item.type == 5) {
                            return (
                                <Form.Item key={index} name={['parameters', item.id]} label={item.name}
                                    rules={[{ required: false, message: `Please input ${item.name}` }]}>
                                    <Select
                                        placeholder="Select"
                                        filterOption={false}
                                        name={item.name}>
                                        {
                                            item.value.split(',').map((value, index) => (
                                                <Option key={index} value={value}>{value}</Option>
                                            ))
                                        }
                                    </Select>
                                </Form.Item>
                            )
                        } else {
                            return (
                                <Form.Item key={index} name={['parameters', item.id]} label={item.name}
                                    rules={[{ required: false, message: `Please input ${item.name}` }]}>
                                    <Input />
                                </Form.Item>
                            )
                        }
                    }) : null
            }

            <p><span style={{ color: '#f5222d', fontSize: 14 }}>*</span> Required</p>
            <Divider />

            <Space>
                <Button onClick={() => props.history.push('/material/list')}
                    icon={<ArrowLeftOutlined />}
                >Back</Button>

                {props.view ?? (
                    <>
                        <Button onClick={() => handleReset()}
                            icon={<ReloadOutlined />}
                        >Reset</Button>

                        <Button
                            type="primary"
                            htmlType="submit"
                            loading={loading}
                            icon={<SaveOutlined />}
                        >Save</Button>
                    </>
                )}
            </Space>
        </Form >
    )
}

export default FormMaterial;