import React, { useState, useEffect } from 'react'
import { Form, Button, Divider, Space, Upload, Modal, message } from 'antd';
import Api from '../../helpers/api';
import {
    ArrowLeftOutlined,
    PlusOutlined,
    LoadingOutlined,
} from '@ant-design/icons';
import handleApiError from '../../helpers/handleApiError';

const api = new Api();

const MaterialImage = (props) => {

    // const [errors, setErrors] = useState({})
    const [loading, setLoading] = useState(false)
    const [form] = Form.useForm();

    const [fileList, setFileList] = useState([])
    const [previewImage, setPreviewImage] = useState('')
    const [showPreviewImage, setShowPreviewImage] = useState(false)
    const [loadingIcon, setLoadingIcon] = useState(false)

    useEffect(() => {
        fetch()
    }, []);

    const fetch = (params = {}) => {
        setLoading(true)

        const { id } = props.match.params

        api.listMaterialImage(id, { ...params })
            .then(response => {
                const { data } = response.data;

                let file = [];
                if (data.length > 0) {
                    data.map((item, index) => {
                        file[index] = {
                            id: item.id,
                            uid: `-${item.id}`,
                            // name: item.id,
                            status: 'done',
                            url: item.image_url_full,
                        }
                    })
                }
                setFileList(file)
                setLoading(false)
            })
            .catch(error => {
                setLoading(false)
                handleApiError(error, props)
            })
    }


    const uploadButton = (
        <div>
            {loadingIcon ? <LoadingOutlined /> : <PlusOutlined />}
            <div style={{ marginTop: 8 }}>{loadingIcon ? 'Uploading...' : 'Upload'}</div>
        </div>
    );

    const handleUpload = (file) => {
        setLoadingIcon(true)

        const { id } = props.match.params

        api.uploadMaterialImage(id, { image: file })
            .then((response) => {
                console.log(response.data);
                message.success('upload image success');
                setLoadingIcon(false)
                fetch()
            })
            .catch(error => {
                if (error.response && error.response.data) {
                    const { errors } = error.response.data
                    setLoading(false)
                    message.error(errors.image);
                    fetch()
                }
            })
    }

    const handleDelete = (value) => {
        Modal.confirm({
            title: `Delete this image?`,
            okText: 'Yes',
            okType: 'danger',
            cancelText: 'No',
            onOk: () => {
                handleDeleteUpload(value)
            },
            onCancel: () => {
                console.log('Cancel');
            },
        });
    }

    const handleDeleteUpload = (value) => {
        setLoading(true);
        const { id } = value

        api.deleteMaterialImage(id)
            .then((response) => {
                console.log(response.data);
                message.success('delete image success');
                setLoading(false);
                fetch()
            })
            .catch(error => {
                if (error.response && error.response.data) {
                    const { errors } = error.response.data
                    setLoading(false)
                    // message.error(message);
                }
            })
    }

    const handlePreview = file => {
        setPreviewImage(file.url || file.preview)
        setShowPreviewImage(true)
    };

    return (
        <Form name="complex-form" labelCol={{ span: 4 }} wrapperCol={{ span: 8 }}
            form={form}>
            <Upload
                disabled={loading}
                name="image"
                listType="picture-card"
                fileList={fileList}
                onPreview={handlePreview}
                onRemove={handleDelete}
                beforeUpload={(file) => {
                    handleUpload(file)
                    return false;
                }}
            >
                {fileList.length >= 10 ? null : uploadButton}
            </Upload>

            <Modal
                open={showPreviewImage}
                footer={null}
                onCancel={() => setShowPreviewImage(false)}
                width={700}>
                <img alt="icon" style={{ width: '100%' }} src={previewImage} />
            </Modal>

            <Divider />
            <Space>
                <Button onClick={() => props.history.push('/material/list')}
                    icon={<ArrowLeftOutlined />}
                >Back</Button>
            </Space>
        </Form>
    )
}

export default MaterialImage;